/*fonts*/

@font-face {
  font-family: 'Lobster';
  font-weight: normal;
  font-style: normal;
  src: url('../fonts/Lobster.eot');
  src: url('../fonts/Lobster.eot?#iefix') format('embedded-opentype'), url('../fonts/Lobster.woff2') format('woff2'), url('../fonts/Lobster.woff') format('woff'), url('../fonts/Lobster.ttf') format('truetype');
}

.openSans {
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
}

.openSans-semibold {
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
}

.lobster {
  font-family: 'Lobster';
  font-weight: 400;
}

/*basic-setting*/

/*header*/

header {
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  /*nav*/
  /*nav-end*/
}

header .top {
  margin-top: 17px;
  margin-bottom: 21px;
}

header nav {
  align-self: flex-end;
}

header nav .telefone {
  font-size: 14px;
  margin-bottom: 15px;
  text-align: right;
  color: #000;
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
}

header nav .telefone__red {
  color: red;
}

header nav .telefone__off {
  margin-left: 20px;
}

header nav .headerNav {
  font-size: 16px;
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
  vertical-align: middle;
  justify-content: flex-end;
}

header nav .headerNav li {
  margin-right: 6%;
}

header nav .headerNav li:last-child {
  margin-right: 0;
}

header nav .headerNav li a {
  color: #222;
}

header nav .headerNav li a:hover {
  text-decoration: none;
  color: red;
}

header .bg {
  display: flex;
  width: 100%;
  height: 700px;
  background: url(../img/bg-1.jpg) no-repeat center center;
  justify-content: center;
  align-items: center;
}

header .bg img {
  max-width: 100%;
}

/*header-end*/

/*main*/

/*order*/

.order {
  display: flex;
  width: 100%;
  margin-top: 23px;
  margin-bottom: 92px;
  padding: 0;
  text-align: center;
  flex-wrap: wrap;
  /*form*/
  /*form-end*/
}

.order__home {
  position: relative;
  top: 10px;
  align-self: flex-end;
}

.order__home img {
  width: 425px;
}

.order__form {
  max-width: 360px;
  margin-top: 60px;
  margin-left: -180px;
  text-align: center;
  border-radius: 10px;
  background-color: #eee3d3;
  align-self: center;
}

.order__form_taitel {
  font-size: 30px;
  margin-top: 37px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
}

.order__form_text {
  font-size: 16px;
}

.order__form input {
  display: block;
  width: 100%;
  max-width: 320px;
  height: 40px;
  margin: 0 auto;
  margin-top: 14px;
  border: none;
  border-radius: 4px;
}

.order__form label {
  font-size: 16px;
  position: absolute;
  left: 30px;
  display: inline-block;
  margin-top: -32px;
  cursor: pointer;
  color: #222;
}

.order__form label span {
  color: red;
}

.order__form input:focus + label {
  display: none;
}

.order__form button {
  font-size: 24px;
  width: 100%;
  max-width: 320px;
  margin-top: 20px;
  margin-bottom: 45px;
  padding-top: 10px;
  padding-bottom: 12px;
  cursor: pointer;
  color: #fff;
  border: none;
  border-radius: 4px;
  background-color: #ffad1e;
  font-family: 'Lobster';
  font-weight: 400;
}

/*order-end*/

.service {
  display: flex;
  width: 100%;
  height: 500px;
  color: #fff;
  background: url(../img/plashaka.jpg) no-repeat center center;
  background-size: cover;
  flex-wrap: wrap;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  justify-content: center;
}

.service__taitel {
  text-align: center;
  align-self: flex-end;
}

.service__taitel h3 {
  font-size: 36px;
}

.service__item {
  display: flex;
  flex-direction: column;
  width: 300px;
  text-align: center;
  white-space: pre-line;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
}

.service__item .plashka {
  width: 96px;
  height: 96px;
  margin: 0 auto;
  margin-bottom: 27px;
}

.service__item span {
  font-size: 20px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
}

.service__item p {
  font-size: 16px;
  margin-top: 20px;
}

/*order-end*/

/*proff*/

.proff {
  display: flex;
  width: 100%;
  text-align: center;
  flex-wrap: wrap;
  justify-content: center;
}

.proff__taitel {
  font-size: 36px;
  width: 100%;
  margin-top: 98px;
  margin-bottom: 20px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
}

.proff__left {
  margin-right: 26px;
}

.proff__right {
  margin-left: 26px;
}

.proff__text {
  font-size: 20px;
  text-align: left;
  white-space: pre-line;
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
}

.proff__text span img {
  margin-bottom: 10px;
}

.proff__text p {
  font-size: 16px;
  margin-top: 18px;
  color: #444;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
}

/*proff-end*/

/*grid*/

.grid {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.grid img {
  width: 100%;
}

.grid img:hover {
  opacity: .4;
}

.grid__right {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: auto;
  flex: 1 1 100%;
  align-content: flex-start;
}

.grid .hover {
  display: -webkit-box;
  background-color: #000000;
}

.fancybox-overlay {
  background: black;
}

.bottom {
  left: 3%;
  width: 91%;
}

/*grid-end*/

/*product*/

.product {
  width: 100%;
}

.product__taitel {
  margin-top: 105px;
  margin-bottom: 10px;
  text-align: center;
  color: #222;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
}

.product__taitel h3 {
  font-size: 36px;
}

.product__taitel span {
  color: red;
}

.product__flex {
  display: flex;
  padding: 0;
  list-style: none;
  flex-wrap: wrap;
}

.product__flex li {
  margin: 0 auto;
  margin-top: 20px;
  margin-right: 20px;
  text-align: center;
}

.product__flex li .items:hover .items__button {
  opacity: 1;
}

.product__flex li .items:hover .card {
  transform: translate(-50%, -50%) scale(1);
}

.product__flex li .card {
  font-size: 12px;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 264px;
  height: 300px;
  transition: 0.3s ease-in-out;
  transform: translate(-50%, -50%) scale(0);
  white-space: pre-line;
  text-decoration: none;
  opacity: .85;
  border: 1px solid #ccc;
  background-color: #fff;
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
}

.product__flex li .card a {
  margin-top: 24px;
  text-decoration: none;
  color: #666;
}

.product__flex li .card__taitel {
  font-size: 18px;
  font-style: italic;
}

.product__flex li img {
  display: block;
}

.product__flex li .line {
  position: relative;
  top: 40px;
  width: 100%;
  height: 10px;
  background: url(../img/product/line.png) no-repeat;
  background-size: cover;
}

.product__flex li .text {
  font-size: 20px;
  white-space: pre-line;
  color: #222;
}

.product__flex li .text__red {
  color: red;
}

.product__flex li:last-child,
.product__flex li:nth-child(7) {
  align-self: flex-end;
}

.product__flex li:last-child .items__button,
.product__flex li:nth-child(7) .items__button {
  height: 300px;
  justify-content: flex-end;
}

.product__flex li .items {
  position: relative;
  display: flex;
  overflow: hidden;
  width: 264px;
  white-space: pre-line;
  justify-content: center;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
}

.product__flex li .items p {
  font-size: 12px;
  font-style: italic;
}

.product__flex li .items__button {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.product__flex li .items__taitel {
  position: relative;
}

.product__flex li .items__taitel a {
  text-decoration: none;
}

/*product-end*/

/*request*/

.request {
  position: relative;
  width: 100%;
  margin-top: 40px;
  background: url(../img/house.jpg) no-repeat center;
  background-color: rgba(0, 0, 0, 0.5);
  background-size: cover;
  background-blend-mode: darken;
}

.request .content {
  display: flex;
  width: 100%;
  height: 700px;
  align-content: center;
}

.request .content__left {
  color: #fff;
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
  align-self: center;
}

.request .content__text {
  font-size: 26px;
  white-space: pre-line;
}

.request .content__text_red {
  padding: 6px;
  background: red;
}

.request .content ul {
  font-size: 20px;
  margin-top: 20px;
  list-style-image: url(../img/icon/ver.png);
}

.request .content ul li {
  margin-top: 10px;
}

.request .content__form {
  width: 100%;
  margin: 0;
  justify-content: flex-end;
  align-self: center;
}

.request .content__form input {
  border: #ccc 1px solid;
}

.request .content__form_item {
  margin: 0;
  background: #fff;
}

/*request-end*/

/*price*/

.price {
  display: flex;
  background: url(../img/bg.png) no-repeat center;
  flex-wrap: wrap;
  justify-content: center;
}

.price__taitel {
  font-size: 16px;
  text-align: center;
  width: 100%;
  margin-top: 97px;
  margin-bottom: 49px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
}

.price__taitel h3 {
  font-size: 36px;
}

.price__taitel h3 span {
  color: red;
}

.price__taitel_red {
  color: red;
}

.price img {
  max-width: 100%;
}

.price__left {
  margin-right: 15px;
  margin-left: 15px;
}

.price__right {
  margin-right: 15px;
  margin-left: 15px;
}

.tabl {
  height: 535px;
  max-width: 555px;
  border: 1px solid #dbdbdb;
  white-space: pre-line;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  font-size: 16px;
}

.tabl td {
  border: 1px solid #d6d6d6;
  height: 68px;
}

.tabl .pdright {
  padding-left: 5%;
}

.tabl .pdleft {
  padding-left: 2.5%;
}

/*price-end*/

/*producer*/

.producer {
  width: 100%;
  padding-bottom: 100px;
  text-align: center;
  background: url(../img/bg-2.jpg) no-repeat center;
  background-size: cover;
}

.producer__taitel {
  width: 100%;
  margin-bottom: 49px;
  padding-top: 97px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
}

.producer__taitel h3 {
  font-size: 36px;
  margin-bottom: 30px;
}

.producer__taitel h3 span {
  font-size: 36px;
  color: red;
}

.producer__taitel span {
  font-size: 20px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
}

.producer__item ul {
  display: flex;
  padding: 0;
  list-style: none;
  justify-content: center;
  flex-wrap: wrap;
}

.producer__item ul li {
  margin-right: 7%;
}

.producer__item ul li:last-child {
  margin-right: 0;
}

/*producer-end*/

/*about*/

.about {
  display: flex;
  margin-bottom: 75px;
  flex-wrap: wrap;
  justify-content: center;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
}

.about__taitel {
  font-size: 36px;
}

.about__text {
  -webkit-mask-image: -webkit-gradient(linear, left -26%, left bottom, from(#000000), to(rgba(0, 0, 0, 0)));
}

.about #block {
  display: none;
}

.about #button-check {
  font-size: 16px;
  padding-right: 20px;
  padding-left: 20px;
  cursor: pointer;
  background: url(../img/arrow.png) no-repeat right;
}

.about .color-norm {
  -webkit-mask-image: -webkit-gradient(linear, left 100%, left bottom, from(#000000), to(rgba(0, 0, 0, 0)));
}

/*about-end*/

/*places*/

.places {
  display: flex;
  width: 100%;
  height: 600px;
}

.places__map {
  width: 100%;
  padding: 0;
}

.places__map iframe {
  width: 100%;
  height: 100%;
  border: none;
}

.places__dispatch {
  margin: 0;
  background: url(../img/homese.jpg) no-repeat center center;
}

.places__dispatch_item {
  margin: 0;
  margin-left: 7%;
  background: #fff;
}

.places__dispatch_item input {
  border: 1px #ccc solid;
}

/*places-end*/

/*footer*/

footer {
  display: flex;
  width: 100%;
  background-color: #222;
}

footer section {
  display: flex;
  height: 199px;
}

footer .telefone {
  font-size: 16px;
  display: flex;
  text-align: center;
  color: #bdbdbd;
  flex-wrap: wrap;
  justify-content: center;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
}

footer .telefone span {
  margin-right: 10px;
  white-space: pre-line;
}

footer .telefone p {
  width: 100%;
  margin: 0;
  justify-item: flex-end;
  align-self: flex-end;
}

footer .avada {
  font-size: 14px;
  display: flex;
  color: #fff;
  flex-wrap: wrap;
  align-self: center;
  justify-content: center;
}

footer .avada span {
  white-space: pre-line;
}

footer .logo {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-self: center;
}

.scrollup {
  position: fixed;
  z-index: 10;
  right: 7%;
  bottom: 73px;
  display: none;
  width: 40px;
  height: 40px;
  text-indent: -9999px;
  opacity: .3;
  background: url('../img/arrow2.png') no-repeat;
}

@media (max-width: 1200px) {
  .product__flex li {
    margin: 0 auto;
  }

  .price__right {
    margin-top: 30px;
  }
}

@media screen and (max-width: 992px) {
  .order__home img {
    max-width: 110%;
  }

  .order__form {
    margin: 0 auto;
    margin-top: 40px;
    justify-content: center;
  }

  .service {
    height: 570px;
  }

  .grid {
    flex-direction: column;
  }

  .grid img {
    height: 500px;
  }

  .grid__right {
    flex-direction: column;
  }

  .grid .hover {
    display: flex;
  }

  .request .content {
    height: auto;
    margin-top: 30px;
    margin-bottom: 30px;
    flex-wrap: wrap;
  }

  .request .content__form {
    justify-content: center;
  }

  .producer__item ul li {
    margin: 0;
    flex-grow: 1;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 720px;
  }
}

@media screen and (max-width: 720px) {
  .service {
    height: auto;
  }

  .places {
    height: auto;
    flex-wrap: wrap;
    justify-content: center;
  }

  .places__map {
    height: 460px;
  }

  .places__map {
    width: calc(100% - 10px);
  }

  .places__dispatch {
    width: calc(100% - 10px);
  }

  .places__dispatch {
    padding-bottom: 40px;
  }

  .places__dispatch_item {
    width: calc(100% - 10px);
  }

  footer section {
    height: auto;
    margin-top: 40px;
    margin-bottom: 40px;
    flex-wrap: wrap;
  }

  footer .telefone {
    margin-bottom: 30px;
  }

  footer .telefone {
    width: calc(100% - 10px);
  }

  footer .avada {
    width: calc(100% - 10px);
  }

  footer .logo {
    width: calc(100% - 10px);
  }

  .scrollup {
    bottom: 150px;
  }
}

@media (max-width: 680px) {
  .producer__item ul li:last-child {
    margin-top: 20px;
  }
}

@media screen and (max-width: 576px) {
  header .top .logo {
    text-align: center;
  }

  header nav .telefone {
    display: flex;
    margin-top: 20px;
    justify-content: center;
    flex-wrap: wrap;
  }

  header nav .headerNav {
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
  }

  header nav .headerNav li {
    margin: 0;
    flex-grow: 1;
  }

  .order__home img {
    max-width: 100%;
  }

  .order__left img {
    max-width: 100%;
  }

  .proff__left {
    width: 100%;
    margin: 0 auto;
  }

  .proff__right {
    width: 100%;
    margin: 0 auto;
  }

  .proff__text {
    text-align: center;
  }

  .grid img {
    height: 250px;
  }

  .producer__taitel h3 {
    font-size: 30px;
  }

  .producer__taitel h3 span {
    font-size: 30px;
  }
}

@media (max-width: 514px) {
  .producer__item ul li:nth-child(3) {
    margin-top: 20px;
  }
}